import React from 'react'
import Loadable from 'react-loadable'

import Loader from '../../layouts/Loader'


const LoadableLogin = Loadable({
    loader: () => import('./Login'),
    loading() {
        return <Loader />
    },
    delay: 250,
})

const LoginComponent = props => (
    <LoadableLogin {...props} />
)

export default LoginComponent
